body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  height: 100vh;
  width: 100vw;
}
.title {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: auto;
  justify-content: center;
  /* padding: 1em; */
  border: 15px solid black;
  background-color: rgb(125, 70, 152);
  color: black;
  font-size: large;
}

.title h1 {
  font-size: 50px;
  font-weight: bold;
  margin: 0;
  padding: 0;
}

.title-Subtitle {
  color: rgb(20, 19, 19);
  padding-top: 0;
}

.title .nav-link{
  display: inline-block;
  padding: 0.5em 1em;
  color: 'yellow';
  font-weight: bold;
  text-decoration: none;
  font-size: 25px;
}

.title .nav-link-active {
  color: #fff;
  border-bottom: 2px solid;
  font-size: 25px;
}

.card-details {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-areas: 
    "name name name . description description description"
    "image image image image . . stats"
    "image image image image . . stats";
}

.card-details h2 {
  grid-area: name;
  font-size: 30px;
  font-weight: bold;
  margin: 0;
  padding: 0;
}

.card-details p {
  grid-area: description;
  font-size: 20px;
  color: rgb(20, 19, 19);
  padding-top: 0;
}

.card-details img {
  grid-area: image;
  max-width: 75%;
  height: 100%;
  object-fit: cover;
}

.card-details table {
  grid-area: stats;
  border: 1px solid black;
  border-radius: 5px;
  background-color: grey;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

.stats h2 {
  font-size: 30px;
  font-weight: bold;
  margin: 0;
  padding: 0;
  text-align: center;
}

/* style the cards */
.card {
  display: flex;
  flex-direction: column;
  box-shadow: 5px 5px 5px rgb(125, 70, 152);
  width: 500px;
  height: 600px;
  border: 1px solid black;
  border-radius: 5px;
  background-color: grey;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

.card img {
  max-width: 100%;
  max-height: 50%;
  object-fit: cover;
  border-radius: 5px;
}

.card button {
  background-color: rgb(209, 85, 147);
  box-shadow: 5px 5px 5px rgb(125, 70, 152);
  border: none;
  border-radius: 5px;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

button:hover {
    box-shadow: 0px 0px 10px rgb(92, 246, 36);
}

.stats {
  /* display: none; */
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}
.card-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  grid-gap: 30px;
  margin: 1rem;
}

.builder {
  display: flex;
  flex-direction: column;
  background-color: black;
  width: 100%;
}

select {
  background-color:rgb(209, 85, 147);
  color: black;
  align-self: center;
  font-size: 30px;
  border-radius: 2px;
}

.Home {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-image: url('https://assets.reedpopcdn.com/Elden-Ring.jpeg/BROK/resize/1200x1200%3E/format/jpg/quality/70/Elden-Ring.jpeg');
  background-size: cover;
  height: 100%;
  padding-right: -30px;
}

